import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '@environments/environment';
import { Observable } from 'rxjs';
@Injectable({
  providedIn: 'root',
})
export class ChatService {
  constructor(private http: HttpClient) {}

  /**
   * This is to ask to the server to create the chat room with this reservation
   * @param reservationId Reservation to create the chat.
   */
  public createReservationChat(reservationId: number): Observable<void> {
    const url = environment.RESERVATION_CHAT.replace(
      '{reservationId}',
      reservationId.toString()
    );
    return this.http.post<void>(url, null);
  }

  /**
   * This is to ask for the token to connect to Twilio.
   */
  public getChatAccessToken(): Observable<{ identity: string; token: string }> {
    return this.http.get<{ identity: string; token: string }>(
      environment.CHAT_ACCESS_TOKEN + '?type=web'
    );
  }
}
