import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '@environments/environment';
import { Observable } from 'rxjs';
import { DishDetails, ModifierGroup, SearchResults } from '@models';

@Injectable({
  providedIn: 'root',
})
export class StaffReservationsService {
  constructor(private http: HttpClient) {}

  /**
   * Function that sends a string as query to searh for dishes.
   * If the search is empty we avoid the petition an only return an empty array.
   * @param restaurantId Id of the restaurant to get the info.
   * @param search Query to send to the server.
   * @returns List of found dishes.
   */
  public searchDishes(
    restaurantId: number,
    search: string
  ): Observable<SearchResults> {
    if (search) {
      const url = environment.SEARCH_DISHES.replace(
        '{restaurantId}',
        restaurantId.toString()
      ).replace('{search}', search);
      return this.http.get<SearchResults>(url);
    } else {
      return new Observable((observer) => {
        observer.next({ results: [] });
        observer.complete();
      });
    }
  }

  /**
   * Get the details of a dish.
   * @param dishId Id of the dish to get the details.
   * @returns The details of the dish.
   */
  public getDishDetails(dishId: number): Observable<DishDetails> {
    const url = environment.GET_DISH_DETAILS.replace(
      '{dishId}',
      dishId.toString()
    );
    return this.http.get<DishDetails>(url);
  }

  /**
   * Funtion that add products to the order.
   * @param orderId Id of the order to add products.
   * @param data products to add.
   */
  public addProductToOrder(
    orderId: number,
    data: {
      id: number;
      amount: number;
      comments: string;
      modifier_group: Array<ModifierGroup>;
    }
  ): Observable<void> {
    const url = environment.ODERS_DISHES.replace(
      '{orderId}',
      orderId.toString()
    );
    return this.http.post<void>(url, data);
  }

  /**
   * Function that tries to remove the dish from an order.
   * @param orderId Id of the order.
   * @param dishUID UID of the dish.
   */
  public removeDishFromOrder(
    orderId: number,
    dishUID: string
  ): Observable<void> {
    const url = environment.ORDERS_DISHES_DETAILS.replace(
      '{orderId}',
      orderId.toString()
    ).replace('{dishId}', dishUID);
    return this.http.delete<void>(url);
  }

  /**
   * Function that closes the reservatio.
   * @param reservationId Id of the reservation.
   * @param hasAssisted Tells if the user assisted to the reservation or not.
   */
  public closeReservation(
    reservationId: number,
    hasAssisted: { assisted: boolean }
  ): Observable<void> {
    const url = environment.CLOSE_RESERVATION.replace(
      '{reservationId}',
      reservationId.toString()
    );
    return this.http.post<void>(url, hasAssisted);
  }
}
