import { Reservation, Dish } from '@models';
import { Component, Input, Output, EventEmitter } from '@angular/core';
import { ReservationsService } from '@services';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialog } from '@angular/material/dialog';
import { RefuseReservationMotiveComponent } from './refuse-reservation-motive/refuse-reservation-motive.component';
import { AddDishToOrderComponent } from './add-dish-to-order/add-dish-to-order.component';
import { Subscription } from 'rxjs';
import { StaffReservationsService } from '@services';
import { StopSoundNotification } from '@utils/sounds.utils';

@Component({
  selector: 'app-reservation',
  templateUrl: './reservation.component.html',
  styleUrls: ['./reservation.component.scss'],
})
export class ReservationComponent {
  /**
   * Reservation to show the info.
   */
  @Input() reservation: Reservation;

  /**
   * It tells to the component if has to show assitances options instead of accept/reject ones.
   */
  @Input() hasToUseAssistanceOptions: boolean;

  /**
   * Event that tells if the reservation was cancelled.
   */
  @Output() reservationCancelled: EventEmitter<void> = new EventEmitter<void>();

  /**
   * Event that tells if the reservation was accepted.
   */
  @Output() reservationAccepted: EventEmitter<void> = new EventEmitter<void>();

  /**
   * Event that tells if the dishes changed from the original list.
   */
  @Output() dishesChanged: EventEmitter<number> = new EventEmitter<number>();

  /**
   * Event that tells if the reservation was closed.
   */
  @Output() reservationClosed: EventEmitter<void> = new EventEmitter<void>();

  /**
   * Variable used to store the subscription that tells us when the crud user modal is closed.
   */
  private afterModalClosesSubscription = Subscription.EMPTY;

  /**
   * Id of the reservation to pass to the chat.
   */
  public reservationIdForChat: number;

  constructor(
    private reservationsService: ReservationsService,
    private snackBar: MatSnackBar,
    private dialog: MatDialog,
    private reservationService: StaffReservationsService
  ) {}

  /**
   *
   * @returns Sum of all of the orders price.
   */
  public get total(): number {
    let total = 0;
    for (const order of this.reservation.orders) {
      if (order.total_in_cents) {
        total = total + order.total_in_cents;
      }
    }
    return total / 100;
  }

  /**
   *
   * @returns Sum of all dishes per amount and diner
   * @param Dishes to sum
   *
   */
  public getTotalAmountPerDiner(dishes: Dish[]): number {
    let total = 0;
    for (const order of dishes) {
      let totalDishAmount = 0;
      if (order.price_in_cents) {
        totalDishAmount = order.amount * order.price_in_cents;
        total = total + totalDishAmount;
      }
    }
    return total / 100;
  }

  /**
   * Sends a POST request to mark a reservation as 'accepted'
   */
  public onReservationConfirmation(): void {
    if (this.reservation.status === 'pending') {
      this.reservationsService
        .confirmReservation(this.reservation.id)
        .subscribe(() => {
          this.openSnackBar('Aceptaste la solicitud', 'Entendido');
          this.reservationAccepted.emit();
        });
    } else if (this.reservation.status === 'checkout') {
      this.reservationsService
        .confirmOrder(this.reservation.id)
        .subscribe(() => {
          this.openSnackBar('Aceptaste la solicitud de rdenes', 'Entendido');
          setTimeout(() => {
            this.reservationAccepted.emit();
          }, 2000);
        });
    }
    StopSoundNotification();
  }

  /**
   * Sends a POST request to mark a reservation as 'accepted'
   */
  public onReservationCancellation(): void {
    const cancellationDialog = this.dialog.open(
      RefuseReservationMotiveComponent,
      {
        width: '540px',
        data: {
          reservationId: this.reservation.id,
        },
      }
    );
    this.afterModalClosesSubscription = cancellationDialog
      .afterClosed()
      .subscribe((hasTheReservationsListToBeReloaded: boolean) => {
        this.afterModalClosesSubscription.unsubscribe();
        if (hasTheReservationsListToBeReloaded) {
          this.reservationCancelled.emit();
        }
      });
    StopSoundNotification();
  }

  /**
   * Created a snackbar event to display a message for the user
   *
   *  @param message Text that will be displayed as the main message in the snackbar
   *  @param action Text for the dismiss button on the snack bar
   */
  private openSnackBar(message: string, action: string): void {
    this.snackBar.open(message, action, {
      duration: 2000,
    });
  }

  public handleAddDish(selectedOrderId: number): void {
    const cancellationDialog = this.dialog.open(AddDishToOrderComponent, {
      width: '680px',
      height: '800px',
      panelClass: 'no-padding',
      data: {
        orderId: selectedOrderId,
      },
    });
    this.afterModalClosesSubscription = cancellationDialog
      .afterClosed()
      .subscribe((hasTheOrderToBeReloaded: boolean) => {
        if (hasTheOrderToBeReloaded) {
          this.dishesChanged.emit(this.reservation.id);
        }
      });
  }

  /**
   * Function that tries to remove the dish from an order.
   * @param orderId Id of the order.
   * @param dishUID UID of the dish.
   */
  public removeDishFromOrder(orderId: number, dishUID: string): void {
    this.reservationService
      .removeDishFromOrder(orderId, dishUID)
      .subscribe(() => {
        this.dishesChanged.emit(this.reservation.id);
      });
  }

  /**
   * When the user tries to open the chat we only take the reservation ID.
   */
  public chatClickHandler(): void {
    this.reservationIdForChat = this.reservation.id;
  }

  /**
   * To close the chat we make the reservation id to undefined.
   */
  public closeChat(): void {
    this.reservationIdForChat = undefined;
  }

  /**
   * Calls the function that closes the reservation at the server.
   * @param hasAssisted Tells if the user assisted to the reservation or not.
   */
  public closeReservation(hasAssisted: { assisted: boolean }): void {
    this.reservationService
      .closeReservation(this.reservation.id, hasAssisted)
      .subscribe(() => {
        this.reservationClosed.emit();
      });
  }

  /**
   * Function that returns a unicode string containing stars.
   * @param numberOfStars The number of stars in the string.
   * @returns A string of stars.
   */
  public getRatingStars(numberOfStars: number): string {
    let ratingStars = '';
    for (let index = 0; index < numberOfStars; index++) {
      ratingStars = ratingStars + ' &#9733;';
    }
    return ratingStars;
  }
}
