import { Component, OnInit, Inject } from '@angular/core';
import { RestaurantService, StaffReservationsService } from '@services';
import { debounceTime, distinctUntilChanged, switchMap } from 'rxjs/operators';
import {
  FormControl,
  FormGroup,
  FormBuilder,
  Validators,
} from '@angular/forms';
import { Restaurant, ProductToAdd, SearchResults, Result } from '@models';
import { Subscription } from 'rxjs';
import { Modifiers } from '@models';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-add-dish-to-order',
  templateUrl: './add-dish-to-order.component.html',
  styleUrls: ['./add-dish-to-order.component.scss'],
})
export class AddDishToOrderComponent implements OnInit {
  /**
   * Variable where the selected restaurant data is saved.
   */
  private restaurantSelected: Restaurant;

  /**
   * Variable used to store the subscription that give us the current restaurant data.
   * We save that subscription so that later we can unsubscribe it.
   */
  private currentRestaurantSubscription = Subscription.EMPTY;

  /**
   * List of validators of the amount form control.
   */
  private amountValidators = [Validators.required, Validators.min(1)];

  /**
   * List of validators of the comments form control.
   */
  private commentsValidators = [Validators.maxLength(500)];

  /**
   * Form used to manage the values of the fields and the selection of modifiers.
   */
  public form: FormGroup = this.fb.group({
    search: new FormControl(''),
    amount: new FormControl(1, this.amountValidators),
    comments: new FormControl('', this.commentsValidators),
  });

  /**
   * This variable tells if the form has been submitted.
   */
  public submitted = false;

  /**
   * List of found elements by the search field.
   */
  public dishes: Array<{ id: number; name: string }> = [];

  /**
   * Id of the selected found element
   */
  public selectedDishId: number = null;

  /**
   * List of modifiers.
   */
  public groupModifiers: Array<Modifiers> = [];

  constructor(
    @Inject(MAT_DIALOG_DATA) private data: { orderId: number },
    private dialogRef: MatDialogRef<AddDishToOrderComponent>,
    private reservationsService: StaffReservationsService,
    private restaurantService: RestaurantService,
    private fb: FormBuilder,
    private snackBar: MatSnackBar
  ) {}

  /**
   * When the component inits we get the info of the selected restaurant.
   * We also init the listener that checks the search field for changes.
   */
  ngOnInit(): void {
    this.getRestaurantInfo();
    this.initSearchListener();
  }

  /**
   * Function that looks for the current restaurant info and save it at the restauranteSelected global variable.
   * After we get the restaurant info we look for its users
   */
  private getRestaurantInfo(): void {
    this.currentRestaurantSubscription = this.restaurantService.currentRestaurant.subscribe(
      (restaurant: Restaurant) => {
        if (restaurant) {
          this.currentRestaurantSubscription.unsubscribe();
          this.restaurantSelected = restaurant;
        }
      }
    );
  }

  /**
   * Function that manages the search functionality.
   */
  private initSearchListener(): void {
    this.form
      .get('search')
      .valueChanges.pipe(
        debounceTime(200),
        distinctUntilChanged(),
        switchMap((search: string) =>
          this.reservationsService.searchDishes(
            this.restaurantSelected.id,
            search
          )
        )
      )
      .subscribe((response: SearchResults) => {
        this.selectedDishId = undefined;
        this.groupModifiers = [];
        this.submitted = false;
        this.dishes = response.results.sort((a: Result, b: Result) => {
          if (a.name > b.name) {
            return 1;
          }
          if (a.name < b.name) {
            return -1;
          }
          return 0;
        });
        this.toFormGroup();
      });
  }

  /**
   * Function that creates the form and its options.
   * This function iterates the group of modifiers list, it checks if the group is of multiple option or not,
   * then creates a form control of type check or radio, using the is of the group to create an id to later collect
   * the information.
   */
  private toFormGroup(): void {
    const formGroup: any = {
      search: new FormControl(this.form.get('search').value),
      amount: new FormControl(
        this.form.get('amount').value,
        this.amountValidators
      ),
      comments: new FormControl(
        this.form.get('comments').value,
        this.commentsValidators
      ),
    };

    for (const group of this.groupModifiers) {
      if (group.multiple_option) {
        for (const modifier of group.modifiers) {
          const checkId = 'check-' + modifier.id;
          formGroup[checkId] = new FormControl(false);
        }
      } else {
        const radioId = 'radio-' + group.id;
        formGroup[radioId] = new FormControl(null);
      }
    }

    this.form = new FormGroup(formGroup);
    this.initSearchListener();
  }

  /**
   * Get the details of the dish.
   * @param dishId Id of the dish to get the details.
   */
  public getDishDetails(dishId: number): void {
    this.selectedDishId = dishId;
    this.groupModifiers = [];
    this.reservationsService
      .getDishDetails(dishId)
      .subscribe((response: any) => {
        this.groupModifiers = response.dish.group_modifiers;
        this.toFormGroup();
      });
  }

  /**
   * Funtion that tries add the selected dishes and info to the order.
   */
  public submit(): void {
    this.submitted = true;
    if (this.form.valid && this.areDishesCorrect) {
      const product = this.getProductToAdd();
      this.reservationsService
        .addProductToOrder(this.data.orderId, product)
        .subscribe(() => {
          this.closeDialog(true);
        });
    } else {
      this.snackBar.open(
        'Favor de revisar la seleccin de elementos',
        'Entendido',
        {
          duration: 15000,
        }
      );
    }
  }

  /**
   * Function that gets the info of the product to add to the order.
   */
  private getProductToAdd(): ProductToAdd {
    const modifierGroup = [];
    for (const group of this.groupModifiers) {
      if (group.multiple_option) {
        const modifiersList = [];
        for (const modifier of group.modifiers) {
          const controlName = 'check-' + modifier.id;
          const formControl = this.form.get(controlName);
          if (formControl.value) {
            modifiersList.push({ id: modifier.id });
          }
        }
        if (modifiersList.length > 0) {
          modifierGroup.push({
            id: group.id,
            modifiers: modifiersList,
          });
        }
      } else {
        const controlName = 'radio-' + group.id;
        const formControl = this.form.get(controlName);
        if (formControl.value !== null) {
          modifierGroup.push({
            id: group.id,
            modifiers: [
              {
                id: formControl.value,
              },
            ],
          });
        }
      }
    }
    const product = {
      id: this.selectedDishId,
      amount: this.form.get('amount').value,
      modifier_group: modifierGroup,
      comments: this.form.get('comments').value
        ? this.form.get('comments').value
        : undefined,
    };
    return product;
  }

  /**
   * Funtion that checks the list of group of modifiers and tells if all are correct.
   * @returns Boolean that tells if all groups are correct.
   */
  private get areDishesCorrect(): boolean {
    for (const group of this.groupModifiers) {
      if (!this.isGroupCorrect(group)) {
        return false;
      }
    }
    return true;
  }

  /**
   * Function that checks if the group is correct.
   * @param group Information about the options that the user can choose.
   * @returns Boolean that tells if the group is correct.
   */
  public isGroupCorrect(group: Modifiers): boolean {
    if (group.multiple_option) {
      const listOfControls = [];
      for (const modifier of group.modifiers) {
        const controlName = 'check-' + modifier.id;
        const formControl = this.form.get(controlName);
        listOfControls.push(formControl);
      }
      let numberOfCheckedElements = 0;
      for (const control of listOfControls) {
        if (control.value) {
          numberOfCheckedElements = numberOfCheckedElements + 1;
        }
      }
      if (numberOfCheckedElements === 0 && group.optional_ask) {
        return true;
      }
      if (
        group.min_options_per_user <= numberOfCheckedElements &&
        group.max_options_per_user >= numberOfCheckedElements
      ) {
        return true;
      }
    } else {
      const controlName = 'radio-' + group.id;
      const formControl = this.form.get(controlName);
      if (formControl.value || group.optional_ask) {
        return true;
      }
    }
    return false;
  }

  /**
   * Change the amount value by one.
   * @param action String with value 'add' or 'subtract' that indicates the operation to do.
   */
  public changeAmountByOne(action: string): void {
    const amount = this.form.get('amount');
    if (action === 'add') {
      amount.setValue(amount.value + 1);
    } else if (action === 'subtract' && amount.value > 1) {
      amount.setValue(amount.value - 1);
    }
  }

  /**
   * Closes the dialog.
   * @param hasTheOrderToBeReloaded Tells to the parent element if the order details should reload.
   */
  public closeDialog(hasTheOrderToBeReloaded: boolean): void {
    this.dialogRef.close(hasTheOrderToBeReloaded);
  }
}
