import { Component, Input, Output, EventEmitter } from '@angular/core';
import * as firebase from 'firebase/app';
import { Router } from '@angular/router';
import { FormBuilder } from '@angular/forms';
import * as moment from 'moment';

@Component({
  selector: 'app-staff-header',
  templateUrl: './staff-header.component.html',
  styleUrls: ['./staff-header.component.scss'],
})
export class StaffHeaderComponent {
  /**
   * Name of the section.
   */
  @Input() public sectionName: string;

  /**
   * Determinates if the calendar hast to be visible.
   */
  @Input() public hasCalendarToBeVisible: boolean;

  /**
   * Tells to the parent element if the action was clicked.
   */
  @Output() actionClicked: EventEmitter<void> = new EventEmitter<void>();

  @Input() hasPeriodSelectToBeVisible: boolean;

  @Output()
  activePeriodChanged: EventEmitter<string> = new EventEmitter<string>();

  /**
   * Tells to the parent element that the date has changed and send this info.
   */
  @Output()
  dateChanged: EventEmitter<moment.Moment> = new EventEmitter<moment.Moment>();

  /**
   * Tells if the menu is open.
   */
  public openMenu = false;

  public form = this.fb.group({
    activeReservationsPeriod: ['today'],
    date: [new Date()],
  });

  constructor(private router: Router, private fb: FormBuilder) {}

  public activePeriodChangedHandler(newValue: string): void {
    this.activePeriodChanged.emit(newValue);
  }

  /**
   * Function called when the calendar date changes.
   * @param event Moment object with the date of the new date.
   */
  public dateChangedHandler(event: moment.Moment): void {
    this.dateChanged.emit(event);
  }

  /**
   * Finish user's session.
   */
  public logout(): void {
    firebase
      .auth()
      .signOut()
      .then(() => {
        localStorage.clear();
        this.router.navigate(['/login']);
      });
  }
}
