import { environment } from '@environments/environment';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { ResponseReservations } from '@models';
import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { IReservation } from '@interfaces';

@Injectable({
  providedIn: 'root',
})
export class ReservationsService {
  constructor(private http: HttpClient) {}

  /**
   * Get the reservations from the server.
   * @param restaurantId Id of the restaurant to find.
   * @param parameters Query to use to filter the reservations.
   */
  public getRestaurantReservations(
    restaurantId: number,
    parameters: any
  ): Observable<ResponseReservations> {
    const url = environment.RESTAURANT_RESERVATIONS.replace(
      '{restaurantId}',
      restaurantId.toString()
    );

    let params = new HttpParams();
    Object.keys(parameters).forEach((parameterKey) => {
      const parameterValue = parameters[parameterKey];

      if (parameterValue) {
        params = params.set(parameterKey, parameterValue);
      }
    });

    return this.http.get<ResponseReservations>(url, { params });
  }

  /**
   * The the details of a reservation.
   * @param reservationId Id of the reservation to get the details.
   */
  public getReservationDetails(
    reservationId: number
  ): Observable<IReservation> {
    const url = environment.RESERVATION_DETAIL.replace(
      '{reservationId}',
      reservationId.toString()
    );

    return this.http.get<IReservation>(url);
  }

  /**
   * Confirm the space reservation.
   * @param reservationId Id of the space reservation to confirm.
   */
  public confirmReservation(reservationId: number): Observable<void> {
    const url = environment.CONFIRM_RESERVATION.replace(
      '{reservationId}',
      reservationId.toString()
    );

    return this.http.post<void>(url, {});
  }

  /**
   * Confirm the orders reservation.
   * @param reservationId Id of the orders reservation to confirm.
   */
  public confirmOrder(reservationId: number): Observable<void> {
    const url = environment.CONFIRM_ORDER.replace(
      '{reservationId}',
      reservationId.toString()
    );

    return this.http.post<void>(url, {});
  }

  /**
   * Cancel the reservation
   * @param reservationId Id of the reservation to cancel.
   * @param reasonData Motive to cancel the reservation.
   */
  public cancelReservation(
    reservationId: number,
    reasonData: string
  ): Observable<void> {
    const httpOptions = {
      headers: new HttpHeaders(),
      body: { reason: reasonData },
    };
    const url = environment.CANCEL_RESERVATION.replace(
      '{reservationId}',
      reservationId.toString()
    );

    return this.http.delete<void>(url, httpOptions);
  }
}
