import {
  Component,
  OnInit,
  OnDestroy,
  Input,
  Output,
  EventEmitter,
} from '@angular/core';
import { ChatService } from '@services';
import * as Twilio from 'twilio-chat';
import { Client } from 'twilio-chat';
import { Channel } from 'twilio-chat/lib/channel';
import { FormControl, FormBuilder, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { NotificationSound } from '@utils/*';


@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.scss'],
})
export class ChatComponent implements OnInit, OnDestroy {
  /**
   * Id of the reservation to conect the chat.
   */
  @Input() private reservationId: number;
  /**
   * Code of the reservation, it is the value showed at the header.
   */
  @Input() public reservationCode: string;
  /**
   * We use this info to get the first two initials to show in the chat.
   */
  @Input() public clientName: string;
  /**
   * Event emitted when the user makes click to close the chat.
   */
  @Output() private closeClicked: EventEmitter<void> = new EventEmitter<void>();
  /**
   * While the configurations load the chat is not visible.
   */
  public startingChat = true;
  /**
   * To know who is the author staff in the chat we compare the staff's email.
   */
  public staffEmail: string;
  /**
   * Form used to manage the message input.
   */
  public form = this.fb.group({
    message: new FormControl('', [Validators.required]),
  });
  /**
   * Channel of the actual chat, it is provided by Twilio.
   */
  private currentChannel: Channel;
  /**
   * List of messages of the chat.
   */
  public messages: Array<any> = [];

  /**
   * Load the sound that the messages will have.
   */
  private messageSound = NotificationSound;

  constructor(
    private fb: FormBuilder,
    private chatService: ChatService,
    private snackBar: MatSnackBar
  ) {}

  /**
   * When the component inits the next steps are made:
   * - We create the the chat room, it is probably that the room has been already created, but it works anyway.
   * - We get the token to connect to Twilio, at the same petition the staff email is obtained, we save it.
   * - We use the token to create the Twilio Client, it contains the Twilio functions to manage the chat.
   * - The client gives us the Channel usgin its unique name, it is formed with the reservation id.
   * - Once we have the channel we save it globally, then we get the history of messages an in the end we init the messages listener.
   * Any error is catched and send an alert, closing this component.
   */
  ngOnInit(): void {
    this.chatService.createReservationChat(this.reservationId).subscribe(() => {
      this.chatService
        .getChatAccessToken()
        .subscribe((chatAccess: { identity: string; token: string }) => {
          this.staffEmail = chatAccess.identity;
          Twilio.Client.create(chatAccess.token)
            .then((client: Client) => {
              client
                .getChannelByUniqueName('rsv' + this.reservationId)
                .then((channel: Channel) => {
                  this.currentChannel = channel;
                  this.getMessagesHistory();
                  this.initMessagesListener();
                  this.startingChat = false;
                })
                .catch(() => {
                  this.errorCatchedWithChat();
                });
            })
            .catch(() => {
              this.errorCatchedWithChat();
            });
        });
    });
  }

  /**
   * When the component is destroyed we remove the messages listener.
   */
  ngOnDestroy(): void {
    this.currentChannel.removeAllListeners('messageAdded');
  }

  /**
   * We get the messages history from the saved channel.
   */
  private getMessagesHistory(): void {
    this.currentChannel.getMessages().then((messages: any) => {
      this.messages = messages.items;
    });
  }

  /**
   * We listen when a message is received.
   */
  private initMessagesListener(): void {
    this.currentChannel.on('messageAdded', (m: any) => {
      if (this.staffEmail !== m.author) {
        this.playSoundMessages();
      }
      this.messages.push(m);
    });
  }

  /**
   * We send the content of the message input.
   */
  public sendMessage(): void {
    if (this.form.valid) {
      const textToSend = this.form.get('message').value;
      this.form.get('message').setValue('');
      this.currentChannel.sendMessage(textToSend);
    }
  }

  /**
   * Using the client's name we get the firs two initials.
   * @returns String with the two first initials of the client name.
   */
  public get clientInitials(): string {
    let initials = '';
    if (this.clientName) {
      const names = this.clientName.split(' ');
      if (names.length === 1) {
        initials = names[0].slice(0, 1);
      } else {
        initials = names[0].slice(0, 1) + names[1].slice(0, 1);
      }
    }
    return initials;
  }

  /**
   * This function sends and alert that something with the chat went wrong, then asks the parent to close this component.
   */
  private errorCatchedWithChat(): void {
    this.snackBar.open(
      'Ocurri un error tratando de conectar el chat',
      'Entendido',
      {
        duration: 15000,
      }
    );
    this.emitCloseAction();
  }

  /**
   * this function asks to the parent to close this component.
   */
  public emitCloseAction(): void {
    this.closeClicked.emit();
  }

  /**
   * Play the message sound.
   */
  private playSoundMessages = (): void => {
    this.messageSound.loop(false);
    this.messageSound.play();
    this.messageSound.volume(1);
  }
}
