import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import * as moment from 'moment';

@Component({
  selector: 'app-reservation-timer',
  templateUrl: './reservation-timer.component.html',
})
export class ReservationTimerComponent implements OnInit {
  /**
   * Unix time that indicates when the reservations is going to expire
   */
  @Input() private expiresAt = 0;

  /**
   * Id of the reservation that represents the timer,
   * We use it to tell to the parent to remove this reservation when the time expires.
   */
  @Input() public reservationId: any;

  /**
   * Event that tells that the expiration time has been reached.
   */
  @Output() public timeHasExpired: EventEmitter<number> =
    new EventEmitter<number>();

  /**
   * Number of seconds until the expiration time is reached.
   */
  public secondsToExpiration = 0;

  /**
   * Number of seconds to start to empty the timer.
   */
  private secondsAsCompleteTime = 600;

  constructor() {}

  /**
   * We init the timer when the component is created.
   */
  ngOnInit(): void {
    this.initTimer();
  }

  /**
   * Takes the expiration time and then apply the timer functionality.
   */
  private initTimer(): void {
    // Each second we check how much time is left until the expiration time.
    const interval = setInterval(() => {
      const expiration = moment(this.expiresAt);
      const now = moment();
      if (expiration.isAfter(now)) {
        const duration = moment.duration(expiration.diff(now));
        this.secondsToExpiration = duration.asSeconds();
        if (this.secondsToExpiration <= 0) {
          clearInterval(interval);
          this.secondsToExpiration = 0;
          this.timeHasExpired.emit(this.reservationId);
        }
      } else {
        clearInterval(interval);
        this.secondsToExpiration = 0;
        this.timeHasExpired.emit(this.reservationId);
      }
    }, 1000);
  }

  /**
   * @returns Time until the expiration time is reached in format MM:SS.
   */
  public get timeToExpire(): string {
    const minutes: number = Math.floor(this.secondsToExpiration / 60);
    return (
      ('00' + minutes).slice(-2) +
      ':' +
      ('00' + Math.floor(this.secondsToExpiration - minutes * 60)).slice(-2)
    );
  }

  /**
   * @returns Percent of hour and a half until the expiration time is reached.
   */
  public get percent(): number {
    return Math.floor(
      (this.secondsToExpiration * 100) / this.secondsAsCompleteTime
    );
  }

  /**
   * @returns Color to apply to the circle.
   */
  public get progressColor(): string {
    return this.percent > 50
      ? '#35B24D'
      : this.percent > 25
      ? '#F2A412'
      : '#571B15';
  }
}
