import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Subscription } from 'rxjs';
import {
  RestaurantService,
  ReservationsService,
  NewRequestAlertService,
} from '@services';
import { Restaurant, Reservation, ResponseReservations } from '@models';
import { map } from 'rxjs/operators';
import { IReservation } from '@interfaces';
import {
  animate,
  group,
  style,
  transition,
  trigger,
} from '@angular/animations';
import * as moment from 'moment';
import { Router } from '@angular/router';

@Component({
  selector: 'app-reservations-crud',
  templateUrl: './reservations-crud.component.html',
  styleUrls: ['./reservations-crud.component.scss'],
  animations: [
    trigger('items', [
      transition(':enter', [
        style({ transform: 'scale(0.5)', opacity: 0 }),
        animate('500ms ease-out', style({ transform: 'scale(1)', opacity: 1 })),
      ]),
      transition(':leave', [
        group([
          style({
            opacity: 1,
            overflow: 'hidden',
          }),
          animate(
            '800ms ease',
            style({
              height: 0,
              paddingTop: 0,
              paddingBottom: 0,
            })
          ),
          animate(
            '200ms',
            style({
              opacity: 0,
            })
          ),
        ]),
      ]),
    ]),
  ],
})
export class ReservationsCrudComponent implements OnInit, OnDestroy {
  /**
   * Text to show at the header.
   */
  @Input() public sectionTitle: string;

  /**
   * Title at the section that is showed when there are no reservations.
   */
  @Input() public emptyReservationsHeader: string;

  /**
   * Subtitle at the section that is showed when there are no reservations.
   */
  @Input() public emptyReservationsSubtitle: string;

  /**
   * Determinates if the header should show the select with the period of time options.
   * If this option is setted to true when the reservations are looked for by default is only going to ask for today's reservation.
   */
  @Input() public hasPeriodOfTimeLogicToBeActive: boolean;

  /**
   * Reservations type to be looked for.
   */
  @Input() private reservationsTypes: any;

  /**
   * Determinates if the assitance buttons are visible.
   */
  @Input() public hasToUseAssistanceOptions: boolean;

  /**
   * Determinates if the calendar at the header is visible.
   */
  @Input() public hasCalendarToBeVisible: boolean;

  /**
   * List of reservations.
   */
  public reservations: Array<Reservation> = [];

  /**
   * Selected reservation.
   */
  public selectedReservation: Reservation = Reservation.empty();

  /**
   * Details of the selected reservation obtained from the server.
   */
  public reservation: Reservation = Reservation.empty();

  /**
   * Variable used to store the subscription that give us the actual restaurant data.
   * We save that subscription so that later we can unsubscribe it.
   */
  private currentRestaurantSubscription = Subscription.EMPTY;

  /**
   * Current restaurant details.
   */
  private restaurant: Restaurant;

  /**
   * Variable used to store the subscription that gives us the reservations from the server.
   * We save that subscription so that later we can unsubscribe it.
   */
  private getReservationsSubscription = Subscription.EMPTY;

  /**
   * Variable used tp store the subscription that gives us the reservation details from the server.
   * We save that subscription so that later we can unsubscribe it.
   */
  private getReservationDetailsSubscription = Subscription.EMPTY;

  /**
   * Listener of when a new reservation arrives.
   */
  private newReservationSubscription = Subscription.EMPTY;

  /**
   * Variable used to store the subscription that tells us when a reservation has changed its status to 'checkout'.
   * We save that subscription so that later we can unsubscribe it.
   */
  private newCheckoutReservationSubscription = Subscription.EMPTY;

  /**
   * Variable used to store the subscription that tells us when there are new In service reservations.
   * We save that subscription so that later we can unsubscribe it.
   */
  private newInServiceReservationSubscription = Subscription.EMPTY;

  /**
   * Variable used to store the subscription that tells us when a reservation has been cancelled.
   * We save that subscription so that later we can unsubscribe it.
   */
  private newCancelledReservationSubscription = Subscription.EMPTY;

  constructor(
    private restaurantService: RestaurantService,
    private reservationsService: ReservationsService,
    private newRequestAlertService: NewRequestAlertService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.getRestaurantInfo();
    this.listenForReservationsUpdates();
    this.listenForReservationsCancellations();
    this.clearStatusDot();
  }

  /**
   * When the component is destroyed we unsubscribe all the subscriptions.
   */
  ngOnDestroy(): void {
    this.currentRestaurantSubscription.unsubscribe();
    this.getReservationsSubscription.unsubscribe();
    this.getReservationDetailsSubscription.unsubscribe();
    this.newReservationSubscription.unsubscribe();
    this.newCheckoutReservationSubscription.unsubscribe();
    this.newCancelledReservationSubscription.unsubscribe();
    this.newInServiceReservationSubscription.unsubscribe();
  }

  /**
   * When the component inits we check if the route is one with a menu option that shows dot.
   * If that's the case we clear the dot.
   */
  private clearStatusDot(): void {
    if (this.isThisTheNewReservationsCRUD) {
      this.newRequestAlertService.hasNewRequest.next(false);
    }
    if (this.isThisTheInServiceReservationsCRUD) {
      this.newRequestAlertService.hasNewInServiceReservation.next(false);
    }
  }

  /**
   * Subscribes to Restaurant service to get the information of the current restaurant
   */
  private getRestaurantInfo(): void {
    this.currentRestaurantSubscription =
      this.restaurantService.currentRestaurant.subscribe(
        (restaurant: Restaurant) => {
          this.restaurant = restaurant;
          if (this.reservationsTypes.status === 'active') {
            this.getReservations('today');
          } else {
            this.getReservations();
          }
        }
      );
  }

  /**
   * Sends a GET request to obtain all pending reservations in a restaurant.
   * @param period Could be 'today' or 'next'.
   * @param date Moment date indicating the day to get the reservations.
   */
  public getReservations(period?: string, date?: moment.Moment): void {
    const params = this.reservationsTypes;
    if (this.hasPeriodOfTimeLogicToBeActive) {
      if (period === 'today') {
        params.from = Math.floor(moment().startOf('day').valueOf() / 1000);
        params.to = Math.floor(moment().endOf('day').valueOf() / 1000);
      } else if (period === 'next') {
        params.from = Math.floor(moment().endOf('day').valueOf() / 1000);
        params.to = undefined;
      }
    }
    if (this.hasCalendarToBeVisible) {
      if (date) {
        params.from = Math.floor(date.startOf('day').valueOf() / 1000);
        params.to = Math.floor(date.endOf('day').valueOf() / 1000);
      } else {
        params.from = Math.floor(moment().startOf('day').valueOf() / 1000);
        params.to = Math.floor(moment().endOf('day').valueOf() / 1000);
      }
    }
    this.reservationsService
      .getRestaurantReservations(this.restaurant.id, params)
      .pipe(
        map((response: ResponseReservations) => {
          const reservations = response.reservations.map((reservation) => {
            return new Reservation(reservation);
          });

          // At the New Requests view the expired reservations are filtered.
          const reservationsStillNotExpired = reservations.filter(
            (reservation) => {
              return !(
                (reservation.status === 'pending' ||
                  reservation.status === 'checkout') &&
                this.isTimeExpired(reservation.expires_at)
              );
            }
          );

          return reservationsStillNotExpired;
        })
      )
      .subscribe((reservations: Array<Reservation>) => {
        this.reservations = reservations;
        this.selectedReservation = this.reservations[0];

        if (this.selectedReservation) {
          this.getSelectedReservation();
        }
      });
  }

  /**
   * Tells if a specific time has already expired.
   * @param expirationTime The time to check.
   * @returns Boolean that tells if the time is expired.
   */
  private isTimeExpired(expirationTime: number): boolean {
    return moment().valueOf() > expirationTime;
  }

  /**
   * Subscribes to socket service to get new requests information
   * Listens for new reservations requests or reservation request cancellations
   */
  private listenForReservationsUpdates(): void {
    if (this.isThisTheNewReservationsCRUD) {
      this.initListenersForNewReservationsSection();
    }
    if (this.isThisTheInServiceReservationsCRUD) {
      this.initListenersForInServiceReservationsSection();
    }
  }

  /**
   * This funtion inits the socket listeners for the posible changes at the New Reservations section.
   */
  private initListenersForNewReservationsSection(): void {
    this.newReservationSubscription =
      this.newRequestAlertService.newPendingReservation.subscribe(
        (reservation: Reservation) => {
          if (reservation) {
            this.reservations.push(reservation);
            if (this.reservations.length === 1) {
              this.selectedReservation = this.reservations[0];
              this.getSelectedReservation();
              this.newRequestAlertService.newPendingReservation.next(null);
            }
          }
        }
      );

    this.newCheckoutReservationSubscription =
      this.newRequestAlertService.newCheckoutReservation.subscribe(
        (reservation: Reservation) => {
          if (reservation) {
            this.reservations.push(reservation);
            if (this.reservations.length === 1) {
              this.selectedReservation = this.reservations[0];
              this.getSelectedReservation();
              this.newRequestAlertService.newCheckoutReservation.next(null);
            }
          }
        }
      );
  }

  /**
   * This funtion inits the socket listeners for the posible changes at the In Service section.
   */
  public initListenersForInServiceReservationsSection(): void {
    this.newInServiceReservationSubscription =
      this.newRequestAlertService.newInServiceReservation.subscribe(
        (areNewInServiceReservations: boolean) => {
          if (areNewInServiceReservations) {
            this.getReservations();
            this.newRequestAlertService.newInServiceReservation.next(null);
          }
        }
      );
  }

  /**
   * Listens for request cancellations
   */
  private listenForReservationsCancellations(): void {
    this.newCancelledReservationSubscription =
      this.newRequestAlertService.newCancelledReservation.subscribe(
        (reservationId: number) => {
          if (reservationId) {
            this.removeReservationFromList(reservationId);
          }
        }
      );
  }

  /**
   * Sends a GET request to obtain the details of a specific reservation
   *
   * @param id number containing the selected reservation ID
   */
  public getSelectedReservation(): void {
    this.getReservationDetailsSubscription = this.reservationsService
      .getReservationDetails(this.selectedReservation.id)
      .pipe(
        map((response: IReservation) => {
          return new Reservation(response);
        })
      )
      .subscribe((reservation: Reservation) => {
        this.reservation = reservation;
      });
  }

  /**
   * On click, finds the selected reservation
   *
   * @param id number cointaining the clicked reservation ID
   */
  public onReservationClick(id: number): void {
    this.reservation = Reservation.empty();
    this.selectedReservation = this.reservations.find((pRequest) => {
      return pRequest.id === id;
    });
    this.selectedReservation.is_new = false;
    this.getSelectedReservation();
  }

  /**
   * Function called when a new period of time is selected.
   * It looks for the reservations at the server at the specific period of time.
   * @param newPeriod Could be 'today' or 'next'.
   */
  public reservationsPeriodChangedHandler(newPeriod: string): void {
    this.getReservations(newPeriod);
  }

  /**
   * Tells if the actual section is the New Reservations one.
   */
  private get isThisTheNewReservationsCRUD(): boolean {
    return this.router.url.endsWith('new-requests');
  }

  /**
   * Tells if the actual section is the In Service one.
   */
  private get isThisTheInServiceReservationsCRUD(): boolean {
    return this.router.url.endsWith('in-service');
  }

  /**
   * Removes a specific reservation and handle to show the correct selected one.
   * @param reservationId The id of the reservation to remove from the list.
   */
  public removeReservationFromList(reservationId: number): void {
    if (reservationId) {
      const index = this.reservations.findIndex((request) => {
        return request.id === reservationId;
      });
      this.reservations.splice(index, 1);
      if (
        this.selectedReservation &&
        this.selectedReservation.id === reservationId
      ) {
        this.selectedReservation =
          this.reservations.length > 0 ? this.reservations[0] : undefined;
        if (this.selectedReservation) {
          this.getSelectedReservation();
        }
      }
    }
  }
}
