import { Component, Inject, OnDestroy } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ReservationsService } from '@services';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-refuse-reservation-motive',
  templateUrl: './refuse-reservation-motive.component.html',
  styleUrls: ['./refuse-reservation-motive.component.scss'],
})
export class RefuseReservationMotiveComponent implements OnDestroy {
  /**
   * Form used to manage the motive of cancelation field.
   */
  public form: FormGroup = this.fB.group({
    motive: ['', [Validators.required, Validators.maxLength(400)]],
  });

  /**
   * Variable used to store the subscription that cancels the reservation.
   * We save that subscription so that later we can unsubscribe it.
   */
  private cancellationSubscription = Subscription.EMPTY;

  /**
   * Name of the visible content.
   */
  public visibleContent = 'motiveForm';

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: { reservationId: number },
    public dialogRef: MatDialogRef<RefuseReservationMotiveComponent>,
    private fB: FormBuilder,
    private reservationsService: ReservationsService,
    private snackBar: MatSnackBar
  ) {}

  /**
   * We unsubscribe all subscriptions when the component is destroyed.
   */
  ngOnDestroy(): void {
    this.cancellationSubscription.unsubscribe();
  }

  /**
   * Handle the user's cancellation request.
   */
  public requestCancellation(): void {
    if (this.form.valid) {
      const reason = this.form.get('motive').value;
      this.cancellationSubscription = this.reservationsService
        .cancelReservation(this.data.reservationId, reason)
        .subscribe(() => {
          this.cancellationSubscription.unsubscribe();
          this.closeDialog(true);
          this.openSnackBar('Cancelaste la solicitud', 'Entendido');
        });
    }
  }

  /**
   * Closes the dialog.
   * @param hasTheReservationsListToBeReloaded Tells to the parent element if the reservations list should reload.
   */
  public closeDialog(hasTheReservationsListToBeReloaded: boolean): void {
    this.dialogRef.close(hasTheReservationsListToBeReloaded);
  }

  /**
   * Created a snackbar event to display a message for the user
   *
   *  @param message Text that will be displayed as the main message in the snackbar
   *  @param action Text for the dismiss button on the snack bar
   */
  private openSnackBar(message: string, action: string): void {
    this.snackBar.open(message, action, {
      duration: 2000,
    });
  }

  /**
   * Set wich content must be visible.
   * @param sectionName Name of the content to set as visible.
   */
  public setVisibleSection(sectionName: string): void {
    this.visibleContent = sectionName;
  }
}
